body {
  margin: 0;
  font-family: var(--body-font);
  background-color: #fff;
}

main {
  font-family: var(--body-font);
  background-color: #fff;
}

.layout {
  height: 100vh;
  background: "cover";
  background-image: url("../../resources/images/banner.svg");
}

.header {
  background-color: #fff;
  box-shadow: 0 0 6px rgba(51, 51, 51, 0.15);
  padding: 0;
  position: sticky;
  top: 0;
  z-index: 1;
  display: flex;
  align-items: center;
  padding: 20px;
}
.header .logo {
  display: flex;
  align-items: center;
  justify-content: center;
}

.header-title {
  color: white;
  margin: 16px;
  text-align: center;
  font-family: var(--body-font);
  font-size: 14px;
  font-weight: 400;
  line-height: 17px;
}

.content {
  padding: 0px 24px;
  background-color: transparent;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
}
.content .calculator-and-summary-wrapper {
  display: grid;
  grid-template-columns: 1fr 0.8fr;
  gap: 20px;
}
.content .main-title {
  font-size: 55px;
  color: white;
  font-weight: 900;
  margin-bottom: 20px;
}
.content .sub-title {
  font-size: 25px;
  color: white;
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
}
@media (min-width: 1110px) {
  .content {
    width: 70%;
  }
}

.previous-button {
  background-color: #f2f4f6;
  border: none;
  margin-right: 20px;
}

/* width */
::-webkit-scrollbar {
  width: 8px;
  background-color: transparent;
}

/* Handle */
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.2);
  background-color: #bebebe;
}

.progress-bar {
  margin-top: -11px;
  margin-bottom: 20px;
}
.progress-bar :global(.ant-progress-inner) {
  height: 3px;
}

.currency-switch {
  display: flex;
  gap: 10px;
  align-items: center;
}

.calculator-button {
  width: 200px;
  border-radius: 8px;
  opacity: 1;
  transform: none;
  min-height: 45px;
  min-width: 80px;
  font-size: 18px;
  font-weight: 600;
  width: fit-content;
  height: 60px;
  background: linear-gradient(135deg, #6252e1, #101e60);
  color: white;
}
.calculator-button :hover {
  background: linear-gradient(135deg, #6252e1, #101e60);
}